/* Defaults
------------------------------------------------------------------------------------- */
:root {
    --backdrop-filter-default: saturate(1.5) blur(1.5em);
    --background-color-light-transparency: rgb(255 255 255 / 60%);
    --background-hover-transition: background-color 0.25s linear;
    --font-family-code: "Fira Code", monospace;
    --font-family-code-font-variation: "Fira Code VF", monospace;
    --color-grey-light: rgb(192 192 192);
    --color-grey-dark: rgb(136 136 136);
    --tw-border-opacity: 1;
    --link-hover-color-default: rgb(252 185 43);
}
